import React, { useEffect, useState } from 'react'
import styles from './QuickCart.module.css'
import topArrow from '../assets/topArrow.png'
import bottomArrow from '../assets/bottomArrow.png'
import leftArrow from '../assets/leftArrow.png'
import rightArrow from '../assets/rightArrow.png'
import { ProductListComp } from 'components/product/ProductListComp'
import axios from 'axios'
import { ProductList } from 'types'
import { Link } from 'react-router-dom'


interface CartItem {
  product: ProductList;
  isSelected: boolean; //   
  quantity: number;
}

export const QuickCart = () => {
  const [isVisible, setIsVisible] = useState(false);
  const [buttonImage, setButtonImage] = useState(topArrow);
  const [startIndex, setStartIndex] = useState(0);
  const [cartItems, setCartItems] = useState<CartItem[]>([])

  const filteredCartItems = cartItems.filter(item => item.isSelected === true);
  useEffect(() => {
    const fetchData = async () => {
      try {
        const response = await axios.get<ProductList[]>('http://175.215.44.128:8080/product');
        const cartItems = response.data.map(product => ({
          product,
          isSelected: true,
          quantity: 1,
        }));
        setCartItems(cartItems);

      } catch (error) {
        console.error('    :', error);
      }
    };
  
    fetchData();
  }, []);

  
  const handelClick = () => {
    setIsVisible(!isVisible);
    setButtonImage(isVisible ? topArrow : bottomArrow );

  }

  const handlePrev = () => {
    if (startIndex > 0) {
      setStartIndex(prevIndex => prevIndex - 1);
    }
  };

  const handleNext = () => {
    if (startIndex < cartItems.length - 4) { //      
      setStartIndex(prevIndex => prevIndex + 1);
    }
  };

  const handleSelectChange = (id: number) => {
    
    const updatedItems = cartItems.map(item => {
        if (item.product.productId === id) {
            return { ...item, isSelected: !item.isSelected };
        } else {
          return item;
        }
    });
    setCartItems(updatedItems);
  };

  const calculateTotal = () => {
    return cartItems
      .filter(item => item.isSelected)
      .reduce((total, item) => total + (item.product.productDiscount * item.quantity), 0).toLocaleString();
  };
  
  const handleQuantityChange = (id: number, delta: number) => {
    const updatedItems = cartItems.map(item => {
      if (item.product.productId === id) {
        const newQuantity = item.quantity + delta;
        return { ...item, quantity: newQuantity >= 1 ? newQuantity : 1 }; //   1 
      } else {
        return item;
      }
    });
    setCartItems(updatedItems);
  };
  
  const selectAll = () => {
    const areAllSelected = cartItems.every(item => item.isSelected);
    const updatedItems = cartItems.map(item => ({
      ...item,
      isSelected: !areAllSelected,
    }));
    setCartItems(updatedItems);
    setStartIndex(0);
  };

  const selectDelete = () => {
    const remainingItems = cartItems.filter(item => !item.isSelected);
    //   cartItems  .
    // isSelected    ,   .
    setCartItems(remainingItems);
    setStartIndex(0)
  };

  const renderListItems = () => {
    return cartItems.slice(startIndex, startIndex + 5).map((item, index) => (
      <div className={styles.cartItemContainer}>
        <span className={styles.checkboxContainer}>
          <input
            id={`checkbox-${item.product.productId}`}
            type="checkbox"
            className={styles.customCheckbox}
            checked={item.isSelected}
            onChange={() => handleSelectChange(item.product.productId)}
          />
          
        </span>
        <div>
          <li key={item.product.productId}>
            <ProductListComp product={item.product} size='110px' fontSize='4px'/>
          </li>
          <div className={styles.quantityContainer}>
            <button className={styles.quantityButton} onClick={() => handleQuantityChange(item.product.productId, -1)}>-</button>
            {item.quantity}
            <button className={styles.quantityButton} onClick={() => handleQuantityChange(item.product.productId, 1)}>+</button>
          </div>
        </div>
      </div>
    ));
  };


  return (
    <div className={styles.quickCartBar}>
      <img className={styles.btnQuickCart} onClick={handelClick} src={buttonImage} alt=" " />
        <div className={`${styles.quickCartContainer} ${isVisible ? styles.show : ''}`}>
          <div className={`${styles.quickCartOff} ${!isVisible ? styles.show : styles.hidden}`}>
             
          </div>
          <div className={styles.quickCartOn}>
            {cartItems.length > 0 ? (
              <div className={styles.cartListContainer}>
                <div className={styles.moveButton}>
                  <img width="35" height="35" src={leftArrow} onClick={handlePrev}/>
                </div>

                <div className={styles.cartList}>
                  <ul className={styles.productList}>
                    {renderListItems()}
                  </ul>
                  
                </div>
                <div className={styles.moveButton}>
                  <img width="35" height="35" src={rightArrow} onClick={handleNext}/>
                </div>
              </div>
            ) : (
              <div className={styles.emptyCartMessage}>
                  
              </div>
            )}


            <div className={styles.totalPriceContainer}>
              <div className={styles.totalPriceTitle}>   </div>
              <div className={styles.totalPrice}>{calculateTotal()} </div>
              <div className={styles.quickCartBtns}>
                <Link to='/order' state={{ cartItems: filteredCartItems }} style={{textDecoration: 'none'}}>
                  <div className={styles.quickOrderbutton}>  </div>
                </Link>
                <div>
                  <div className={styles.quickCartbutton} onClick={selectAll}>   /  </div>
                </div>
                <div>
                  <div className={styles.quickCartbutton} onClick={selectDelete}>    </div>
                </div>
                </div>
            </div>
            
          </div>
        </div>
    </div>
  );
};
