import React from 'react';
import { CartItem } from 'types';
import styles from './MyCartListComp.module.css';
import styles_m from './MyCartMobile.module.css';

interface OrderItemProps {
    orderItem: CartItem;
}

export const OrderItemListComp: React.FC<OrderItemProps> = ({orderItem}) => {
  return (
    <div className={styles.myCartListComp}>
        <table>
            <colgroup>
                <col width="30px"></col>
                <col width="140px"></col>
                <col></col>
                <col width="150px"></col>
                <col width="150px"></col>
            </colgroup>
            <thead>
                <tr>
                    <th data-label=""></th>
                    <th data-label=""></th>
                    <th data-label="  ">  </th>
                    <th data-label=""></th>
                    <th data-label="" style={{textAlign: 'center'}}></th>
                    <th data-label=""></th>
                </tr>
            </thead>
            <tbody>
                <tr>
                    <td data-label="">

                    </td>
                    <td className={styles.orderImg}  data-label="">
                        <img src={`./upload/${orderItem.product.productImgPath}`} alt="" style={{ width: 140, height: 140 }}></img>
                    </td>
                    <td className={styles.orderName} data-label="  ">{orderItem.product.productName}</td>
                    <td className={styles.orderPrice} data-label="">{orderItem.product.productDiscount.toLocaleString()}</td>
                    <td className={styles.orderQuantity} data-label="">
                        <div className={styles.quantityContainer}>
                            {orderItem.quantity}
                        </div>
                    </td>
                    <td className={styles.orderTotal} data-label="">{(orderItem.product.productDiscount * orderItem.quantity).toLocaleString()}</td>
                </tr>
            </tbody>
        </table>
        <div className={styles_m.mobileContainer}>
            <div className="checkBox">
                
            </div>

            <div>
                <img src={`./upload/${orderItem.product.productImgPath}`} alt="" style={{ width: 100, height: 100, borderRadius: 10}}/>
            </div>
            <div className={styles_m.orderInfo}>
                <div className={styles_m.orderName}>
                    {orderItem.product.productName}

                </div>

                <div className={styles_m.orderOption}>
                    
                </div>
                <div className={styles_m.quantityContainer}>
                    : {orderItem.quantity}
                </div>
    
                <div className={styles_m.orderTotal}>{(orderItem.product.productDiscount * orderItem.quantity).toLocaleString()}</div>
            </div>
        </div>
    </div>
  )
}
