import React, { useEffect, useRef, useState } from 'react';
import Cookies from 'js-cookie';
import { ProductList } from 'types';
import styles from './RecentProducts.module.css'; //   css  .
import { Link } from 'react-router-dom';

const RecentProducts: React.FC = () => {
    const [recentProducts, setRecentProducts] = useState<ProductList[]>([]);
    const productListRef = useRef<HTMLUListElement>(null);

    useEffect(() => {
        const products = Cookies.get('recentProducts');
        if (products) {
            setRecentProducts(JSON.parse(products));
        }

    }, []);

    
    const scrollProductList = (direction: 'up' | 'down') => {
        if (productListRef.current) {
            const { current: list } = productListRef;
            //     
            const scrollAmount = direction === 'down' ? -list.offsetHeight-20 : list.offsetHeight+20;
            productListRef.current.scrollTop -= scrollAmount;
        }
    };
    
    return (
        <div className={styles.recentProductsContainer}>
            <p className={styles.title}>  </p>
            <div>
                <ul ref={productListRef}>
                    {recentProducts.map(product => (
                        <li key={product.productId}>
                            <Link to='/detail' > <img src={`./upload/${product.productImgPath}`} alt="" className={styles.recentProductImg}/> </Link>
                        </li>
                    ))}
                </ul>

            </div>
            <div className={styles.buttonContainer}>
                <button onClick={() => scrollProductList('up')} className={styles.scrollButton}></button> 
                <button onClick={() => scrollProductList('down')} className={styles.scrollButton}></button>
            </div>
        </div>
    );
};

export default RecentProducts;
