import { DetailTabComp } from 'components/DetailTabComp';
import React, { useEffect, useState } from 'react'


export const Detail:React.FC = () => {

    const [isSticky, setIsSticky] = useState(false);

    //   
    const handleScroll = () => {
        const scrollTop = window.scrollY;
        const sticky = scrollTop > 100; // 100px    sticky  
        setIsSticky(sticky);
    };

    //       
    useEffect(() => {
        window.addEventListener('scroll', handleScroll);

        //      
        return () => {
        window.removeEventListener('scroll', handleScroll);
        };
    }, []); //            



  return (
    <div className="bg-white text-gray-700">

            <main className="container mx-auto my-8 p-4">
                <div className="flex flex-wrap md:flex-nowrap items-stretch">
                    <div className="w-full md:w-1/2 p-4">
                        <img src="../upload/1.jpg" alt="" className="w-full h-96 object-cover m-auto rounded shadow-lg " />
                    </div>
                    <div className="w-full md:w-1/2 border-t-2 border-b-2 border-blue-700">
                        <h1 className="text-2xl font-bold border-b-2 border-gray-200 p-4"> 12-13 1kg( 77g )</h1>
                        <div className="text-start border-b-2 border-gray-200 px-4 py-1">
                            <div className="grid grid-cols-5">
                                <div className="font-bold"></div>
                                <div className="col-span-4 text-xl">38,000</div>
                            </div>
                        </div>
                        <div className="grid grid-rows-3 text-start border-b-2 border-gray-200 px-4 py-1">
                            <div className="grid grid-cols-5">
                                <div className="font-bold"></div>
                                <div className="col-span-4"></div>
                            </div>
                            <div className="grid grid-cols-5">
                                <div className="font-bold"></div>
                                <div className="col-span-4"></div>
                            </div>
                            <div className="grid grid-cols-5">
                                <div className="font-bold"> </div>
                                <div className="col-span-4"></div>
                            </div>
                            
                        </div>
                        <div className="border-b-2 border-gray-200 px-4 py-2">
                            <label htmlFor="option-select" className="mb-2"><strong>[]</strong>  </label>
                            <select id="option-select" className="w-full border border-gray-300 rounded p-2">
                                <option>12-13 </option>
                            </select>
                        </div>
                        <div className="border-b-2 border-gray-200 px-4 py-1">
                            <div className="text-end"> : 38,000 (1)</div>
                        </div>
                        <div className="flex justify-center space-x-2 py-4">
                            <button className="bg-blue-700 text-white px-4 py-2 rounded"></button>
                            <button className="border border-gray-300 px-4 py-2 rounded"> </button>
                        </div>
                    </div>
                </div>

                <div className="my-8 w-full ">
                    <h2 className="text-2xl font-bold my-4">   !</h2>
                    <div className="flex overflow-x-auto gap-4">
                        <div className=''>
                            <div className="w-48 ">
                                <img src="../upload/1.jpg" alt="" className="w-full h-48 object-fill rounded shadow-lg" />
                            </div>
                            <div className="text-center"> 1KG</div>
                            <div className="text-center">40,000 </div>
                        </div>
                    </div>
                </div>

                <div className={"my-8 w-full"}>
                    <DetailTabComp />
                </div>
            </main>


        </div>
    );
}
